@import url(https://fonts.googleapis.com/css2?family=Bilbo&family=Spectral:wght@200;800&display=swap);
.Character__attack-list{font-family:'Bilbo', cursive;display:flex;justify-content:center}.Character__attack{font-size:1.2em;margin:0 .5em;border-radius:3px;text-transform:lowercase;cursor:pointer}.Character__specific{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:1em}

.Gandalf{position:absolute;bottom:0;left:3vh}.Gandalf .gandalf-sprite{height:35vh}

.Sauron{position:absolute;bottom:0;right:3vw}.Sauron .sauron-sprite{height:70vh}

body{margin:0;font-family:'Spectral', serif}.App{position:relative;width:100vw;height:100vh;background:#eee;font-size:2em;display:flex;flex-direction:column;align-items:center}.App header{font-size:1.75em;font-weight:800;color:#939393;margin:1em 0;letter-spacing:2px}

